.main-menu-line{
    display: grid;
    grid-template-areas: "icon nam .";
    grid-template-columns: 80px 400px;
    /* direction: ltr; */
}
.icon{
    grid-area: icon;
    vertical-align: text-bottom;
    display: inline;
}
.nam{
    grid-area: nam;
    line-height: 40px;
    font-size: 0.8rem;
}