.service{
    display: grid;
    grid-template-areas: '. title title .' '. content img .' '. . . .';
    grid-template-rows: 10px 400px 10px;
    grid-template-columns: 10px 1fr 1fr 10px;
    /* border: 1px solid; */
    column-gap: 50px;

}
.service-content{
   grid-area: content;
   border:0px solid;
  text-align: justify;
}
.service-img{
    grid-area: img;
    border:0px solid;
    border-radius: 15px ;
}
.service-title{
   /* grid-area: title; */
   text-align: start;
   color: rgb(29,29,51,0.8);

}
@media only screen and (max-width:900px){
    .service{
       
        grid-template-areas: '. title title .' 'img img img img' 'content content content content' '. . . .';
        grid-template-rows: 50px 300px 200px 60px;
        grid-template-columns: 10px 1fr 1fr 10px;
        /* border: 0px solid; */
        /* border: 2px solid; */
    }
    .service-title{
        grid-area: title;
    }
}