/* // project data object {name,type,images:[],frontImage,discription,client,liveLink} */
.add-project-cont{
    display: block;
    /* border: 1px solid red; */
    height: 100%;
    overflow: scroll;
}
.pr-form {
    /* border: 3px solid red; */
    /* overflow: scroll; */

    /* height: 100%; */
    background-color: #FFFFFF;
    /* width: 200px; */
    display: grid;
    gap: 10px;
    grid-template-areas:". . ." " . title ." ". name . " ". type ." ". image ."" . disc ." ". client ." ". link ." ". images ." ". pr-sub ." ". . .";
    grid-template-rows:20px repeat( 9 , 80px) 10px;
    grid-template-columns: 1fr 450px 1fr;
    /* padding: 20px; */
}
.pr-form label{
    color: rgba(0, 0, 0, 0.6);
    font-size: 19px;
}
.pr-form input, .pr-form textarea{
    border-radius: 5px;
    padding-left: 20px;
    width: calc(100% - 24px);
    height: 40px;
    font-size: 17px;
}
.pr-name{
    grid-area: name;
}
.pr-type{
    grid-area: type;
}
.pr-image{
    grid-area: image;
}
.pr-disc{
    grid-area: disc;
}
.pr-client{
    grid-area: client;
}
.pr-link{
    grid-area: link;
}
.pr-images{
    grid-area: images;
}
.pr-sub{
    grid-area: pr-sub;
    padding-left: 0px !important;
    background: #000000;
    border: 0px;
    color: white;
    height:35px;
    width: 100%;
    margin: 0px !important;
    font-size: 17px;
    border-radius: 5px;
}
.pr-sub:hover{
    background-color: black;
    color: green;
}
