.hero-Cont{
    background-color: black;
    width: 100vw;
}
.hero-section{
  position: relative;
  /* border:1px solid; */
  height: 100vh;
  width: 100%;
  
}
.hero-sec-text{
  
}
#background-video{
    object-fit: cover;
    top:0px;
    left:0px;
    right: 0px;
    bottom: 0px;
    height: 100% !important;
    width: 100% !important;
    position: absolute;
}
.over-lay{
  background-color: rgba(0, 0, 0, 0.521);
  position: absolute;
  top:0px;
  left:0px;
  bottom: 0px;
  right: 0px;
  z-index: 1;
  color: #fff;
}
.over-lay h1{
  letter-spacing: 10px;
  font-size: 3.5em;
  color: rgb(55,176,200);
  text-align: center;
  text-shadow:     0 1px 0 #010307c2,
  0 2px 0 #010307c2,
  0 3px 0 #010307a8,
  0 4px 0 #010307b4,
  0 5px 0 #010307ad,
  0 6px 0 #010307be,
  0 7px 0 #010307a4,
  0 8px 0 #010307bb,

  0 0 5px rgba(255, 255, 255, 0.637),
 0 1px 3px rgba(255, 255, 255, 0.644),
 0 3px 5px rgba(255, 255, 255, 0.719),
0 5px 10px rgba(255, 255, 255, 0.2),
0 10px 10px rgba(252, 252, 252, 0.705),
0 20px 20px rgba(255, 255, 255, 0.753);
 


}
.over-lay p{
  color: rgb(55,176,200);
  font-size: 1.5em;
  border-left: 3px solid rgba(100, 148, 237, 0.589);
  border-top: 3px solid rgba(100, 148, 237, 0.589);
  /* padding-left: 10px; */
  border-style: groove;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 20px;
  padding: 20px;
  line-height: 40px;
  letter-spacing: 2px;
 
}
.ctn{
  background-color: rgb(20,23,45,0.8);
  border-radius: 7px;
  line-height: 40px;
  font-size: 22px;
  color: white;
  width: 250px;
  cursor: pointer;
  border:1px solid

}
.ctn span{
  vertical-align: middle;
  color: rgba(100, 148, 237, 0.589);
}
.ctn:hover{
  background-color:rgba(100, 148, 237, 0.589);
}
  video.fullscreen::-webkit-media-controls {
    display: none !important;
  }
