    padding: 0px;
} */
@media only screen and (max-width:900px){
  .hero-sec-text{
    padding: 100px 20px 20px 20px;
  }
  .hero-sec-adjacent{
    display: none;
  }
  .over-lay h1{
    font-size: 60px;
    text-align: center;  
    line-height: 150px;
    /* border: 1px solid; */
  }
}