.contact-form{
    /* display: flex; */
    /* flex-direction: column;
    /* align-items:center ; */
    /* justify-content:space-between; */ 
    /* min-height: fit-content; */
    max-width: 98%;
    /* width: 100%; */
    /* margin-bottom: 50px; */
    padding-bottom: 40px;
    /* padding: 10px; */
    background-color: white !important;
    /* border: 1px solid red; */
    /* height: 535px; */
    margin: 50px 0 50px 0;
}

.form-row{

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width:80% !important;
    /* border: 1px solid; */
}
.form-row input{
    width: 35% !important;
    
}
.contact-text-area{
    margin-top: 35px;
    border: 1px solid #111;
    border-radius: 20px;
    height: 50px;
    transition: 1000;
    padding: 11px 26px;
    width:100% ;

}
.contact-form textarea{
    margin-top: 35px;
    border: 1px solid #111;
    border-radius: 20px;
    background-color: #ffffff;
    transition: 1000;
    padding: 11px 26px;
}
.contact-submit{
    width: 219px ;
    height: 55px ;
    cursor: pointer ;
    color: #333 ;
    border-radius: 20px;
    border: 1px solid;
    background-color: #37B0C8;
    margin-top: 35px;
}
.contact-submit:hover{
    background-color: #00101E !important;
    transition: 1000ms;
    color:#ffffff
}
@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  @media only screen and (max-width:700px) {
    input[type=submit]{
      transform: translate(0%,0%);
      position: relative;
      /* left:50%; */
      margin-top: 25px;

    }
    .form-row input{
        width: 25% !important;
        height: 30px !important;
        
    }
  }