@import 'react-round-carousel/src/index.css';
.carousel__slide{
    border:0px !important;
    border-radius: 3px;
    cursor: pointer;
    /* width:100px !important */
}
.carouselp{
    /* width:100px !important;
    font-size: 10px; */
}
.carouselp img{
    /* width: 100px !important; */
}
.carousel__slide{
    background-color: rgba(192, 13, 13, 0.747) !important;
    border:0px solid !important;
    /* position: relative; */
    height: 400px !important;
    width:400px !important;
    /* object-fit: cover !important; */

}
.carousel__slide img{
    /* border:1px   ; */
    position: absolute;
    height: 400px !important;

    top:50%;
    transform: translate(0%,-50%);
    object-fit: cover !important;

}
.slider-cont{
    /* margin: 100px 0 50px 0; */
    display: block;
    position: relative;
    height: 600px;
    /* border: 1px solid; */

}

@media only screen and (max-width:900px){
    .carousel__slide{
        background-color: rgba(128, 128, 128, 0.747) !important;
        border:0px solid !important;
        /* position: relative; */
        height: 100px;
        width:100px !important;
    }
    .slider-cont{display: none;}
}