.slider-title{
    border-radius:5px ;
    padding: 0 0 0 20px !important;
    background-color: rgba(128, 128, 128, 0.13);
    width: 90%;
    text-align: start;
    line-height: 100px;
    position: absolute;
    transform: translate(5%,-30%);
    top:0px;
    left:50%px;
}